      position: relative;
      overflow: hidden;
  }
  .swiper-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
 .swiper-wrap.dragging{
    -webkit-transition: none;
    transition: none;
}
.swiper-wrap> div {
  overflow-x:hidden; 
  -ms-flex-negative: 0; 
      flex-shrink: 0;
  width: 100%;
  margin: 0px;
  padding: 0px;
}

::-webkit-scrollbar  
{  
    width: 0px;  
}  

.tab-title-container{
    position: relative;
    display: table;
    margin: 0 auto;
    list-style: none;
    border-bottom: 1px solid #dddddd;
}
.tab-title{
    height: 35px;
    line-height: 35px;
    position: relative;
    display: table-cell;
    width: 1%;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
}
.tab-title.active,.tab-title:active {
    border-bottom: 2px solid #36acf4;
    color: #36acf4;
}



